/** @jsx jsx */
import { jsx, Styled } from "theme-ui";

import { graphql, Link } from "gatsby";
import { mapCraft } from "./mappings";

import { useEffect, useState } from "react";
import InstantSearch from "@components/InstantSearch";
import Layout from "@components/Layout";
const Impacts = (props) => {
  const { category } = mapCraft(props.data.craft);

  const { title } = category;

  useEffect(() => {
    if (title) {
      console.log(title);
      setRefinements({
        ...defaultRefinements,
        socialImpacts: [title],
      });
    }
  }, []);

  const [defaultRefinements, setRefinements] = useState({
    type: [],
    socialImpacts: [],
  });

  return (
    <Layout sticky={true} title={title}>
      <InstantSearch
        refinementType="socialImpacts"
        defaultRefinement={defaultRefinements.socialImpacts}
        isSearch={false}
        heading={title}
      />
    </Layout>
  );
};

export default Impacts;

export const query = graphql`
  query SocialImpactsQuery($id: Craft_QueryArgument) {
    craft {
      category(id: [$id]) {
        ... on Craft_socialImpacts_Category {
          title
        }
      }
    }
  }
`;
